@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);

.SearchModule {
  background: rgb(218, 221, 218);
  width: 95%;
  height: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  transition: 1s ease;
}


.App{
    font-family: Roboto;
    display: flex;
    width:100%;
    height: auto;
}
.search-bar {
  border-radius: 4px;
  padding: 8px;
  margin: 6px 0 24px 0;
}

.error-message {
  color: red;
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .search-bar {
    padding: 6px;
    margin: 6px 0 12px 0;
  }

  .error-message {
    color: red;
    font-size: 16px;
    padding: 0px;
    text-align: center;
    margin: 0;
  }
}

.SearchResultBoard{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: auto;
    background: rgb(218, 221, 218);
    padding: 8px;
}

